.btn {
  display: inline-block;
  position: relative;
  padding: var(--btn-padding-h) var(--btn-padding-w);
  border-radius: 16px;
  color: var(--color-white);
  background-color: var(--btn-background);
  line-height: 1;
  font-size: 24px;
  font-weight: 600;
  cursor: pointer;
  &:hover {
    background-color: var(--btn-background-hover);
  }
  &:active,
  &:focus {
    background-color: var(--btn-background-focused);
  }
  &-disabled {
    pointer-events: none;
    background-color: var(--btn-background-disabled);
  }
  &-icon {
    padding: var(--btn-padding-h);
    svg {
      width: 22px;
      height: 22px;
      vertical-align: middle;
      fill: var(--color-white);
    }
    &-left {
      svg {
        margin-right: 4px;
      }
      padding: var(--btn-padding-h) var(--btn-padding-w);
    }
    &-right {
      svg {
        margin-left: 4px;
      }
      padding: var(--btn-padding-h) var(--btn-padding-w);
    }
  }
  &-small {
    padding: var(--btn-padding-h-small) var(--btn-padding-w-small);
    &.btn-icon {
      padding: var(--btn-padding-h-small);
      svg {
        width: 18px;
        height: 18px;
      }
      &-left {
        padding: var(--btn-padding-h-small) var(--btn-padding-w-small);
        svg {
          margin-right: 2px;
        }
      }
      &-right {
        padding: var(--btn-padding-h-small) var(--btn-padding-w-small);
        svg {
          margin-left: 2px;
        }
      }
    }
  }
  &-large {
    padding: var(--btn-padding-h-large) var(--btn-padding-w-large);
    &.btn-icon {
      padding: var(--btn-padding-h-large);
      svg {
        width: 30px;
        height: 30px;
      }
      &-left {
        padding: var(--btn-padding-h-large) var(--btn-padding-w-large);
        svg {
          margin-right: 6px;
        }
      }
      &-right {
        padding: var(--btn-padding-h-large) var(--btn-padding-w-large);
        svg {
          margin-left: 6px;
        }
      }
    }
  }
  &-empty {
    background-color: transparent;
    color: var(--color-primary);
    &:hover {
      background-color: var(--btn-background-hover-empty);
    }
    &:active,
    &:focus {
      background-color: var(--btn-background-focused-empty);
    }
    &.btn-icon {
      svg {
        fill: var(--btn-color);
      }
    }
  }
  &-br {
    border: 2px solid var(--btn-border);
  }
}

.link {
  color: var(--color-primary);
  font-weight: 600;
  font-size: 24px;
  &:hover {
    text-decoration: underline;
  }
}
