.hero {
  &__wrapper {
    @include flex-all-sb;
    @include n {
      flex-direction: column;
    }
  }
  &__data {
    .subtitle {
      background: #ffddbf;
      border-radius: 10px;
      padding: 12px 24px;
      display: inline-flex;
      align-items: baseline;
      font-weight: 600;
      font-size: 24px;
      line-height: 1;
      margin-bottom: 24px;
      img {
          width: 24px;
          height: auto;
          margin-right: 10px;
      }
    }
    .title {
        margin-bottom: 40px;
        @include t {
          font-size: 50px;
        }
    }
  }
  &__img {
    img {
      vertical-align: bottom;
    }
  }
}
