html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: var(--font-base);
  font-size: 20px;
  font-weight: 400;
  color: var(--color-text);
  height: 100%;
  background-color: var(--bg-base);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: var(--color-primary);
}

.wrapper {
  //overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
}

header {
  flex: 0 0 auto;
}
footer {
  flex: 0 0 auto;
}
.main {
  flex: 1 0 auto;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

p {
  margin-bottom: 8px;
}

hr {
  border: none;
  border-bottom: 0.0001rem solid;
  border-color: #979799;
}

.align-center {
  text-align: center;
}

h1.title {
  color: var(--color-title);
  font-weight: 600;
  font-size: 64px;
}

h2.title {
  color: var(--color-title);
  font-weight: 600;
  font-size: 40px;
}
