.lets {
  padding: 64px 0;
  &__wrapper {
    .title {
      font-weight: 600;
      font-size: 64px;
      margin-bottom: 56px;
    }
  }
  &__footer {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      @include s {
          flex-direction: column;
          align-items: flex-start;
      }
  }
  &__social {
    @include s {
        margin-top: 20px;
    }
      .social-icon {
          margin: 0 12px;
      }
      .icon {
          width: 32px;
          height: 32px;
          fill: var(--color-primary);
      }
  }
}
