.reviews {
  padding: 88px 0;
  .desc {
    margin-bottom: 96px;
  }
  &__slide {
    &-name {
      font-size: 20px;
      line-height: 25px;
      font-weight: 700;
      text-align: center;
    }
    &-txt {
      font-size: 20px;
      text-align: center;
      max-width: 390px;
      margin: 0 auto;
      margin-bottom: 30px;
      line-height: 1.5;
    }
  }
  .slider-arrow {
    position: absolute;
    width: 32px;
    height: 32px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    cursor: pointer;
    &.slider-arrow-next {
      left: auto;
      right: 0;
    }
    svg {
      width: 28px;
      height: 28px;
      fill: var(--color-text);
    }
  }
}
