.about {
    background-color: var(--bg-base-alt);
    margin-bottom: 64px;
    @include n {
        padding-top: 64px
      }
    &__wrapper {
        @include flex-all-sb;
        @include n {
            flex-direction: column;
          }
    }
    &__data {
        max-width: 460px;
        .title {
            margin-bottom: 12px;
        }
        .info {
            margin-bottom: 40px;
        }
        p {
            font-weight: 500;
            line-height: 1.5;
        }
    }
    &__img {
        img {
            vertical-align: bottom;
        }
    }
}