.work {
  background-color: var(--bg-base-alt);
  padding: 88px 0;
  &__wrapper {
    .title {
      margin-bottom: 40px;
      @include n {
        text-align: center;
      }
    }
  }
  &__item {
    background: #fff8f2;
    border-radius: 24px;
    padding: 41px 72px;
    margin-bottom: 28px;
    position: relative;
    overflow: hidden;
    @include flex-all-sb;
    z-index: 1;
    @include m {
      padding: 15px 20px;
    }
    @include n {
      flex-direction: column-reverse;
    }
    &-data {
        max-width: 404px;
        padding: 90px 0;
        @include m {
          text-align: center;
        }
      .title {
        color: var(--color-title);
        font-weight: 600;
        font-size: 32px;
        margin-bottom: 4px;
      }
      .info {
        font-weight: 500;
        font-size: 24px;
        margin-bottom: 24px;
      }
    }
    &-img {
      
        img {
            position: absolute;
            top: 0;
            right: 0;
            height: 100%;
            z-index: -1;
            @include n {
              position: relative;
            }
        }
    }
  }
}
