:root {

  --color-primary: #4D5566;
  --color-black: #000;
  --color-white: #fff;
  --color-title: #403930;
  --color-text: #594F43;
  --color-button: #4D5566;
  --color-light: #F3F2FF;
  --color-body: #ffffff;

  --color-icon: #000;

  --bg-base: #FFEED9;
  --bg-base-alt: #FFDDBF;

  // Fonts 
  --font-base: "Eczar", sans-serif;

  // Button
  --btn-color: var(--color-light);
  --btn-border: var(--color-primary);
  --btn-background: var(--color-primary);
  --btn-background-hover: rgb(37, 37, 37);
  --btn-background-focused: rgb(19, 19, 19);
  --btn-background-disabled: rgb(43, 43, 43);

  --btn-background-hover-empty: #ffddbf;
  --btn-background-focused-empty: #f3ceae;

  --btn-padding-h: 20px;
  --btn-padding-w: 28px;
  --btn-padding-h-small: calc( var(--btn-padding-h) - 2px);
  --btn-padding-w-small: calc( var(--btn-padding-w) - 4px);
  --btn-padding-h-large: calc( var(--btn-padding-h) + 2px);
  --btn-padding-w-large: calc( var(--btn-padding-w) + 4px);


}
