.nav {
  @include m {
    order: 2;
    display: block;
    width: 100%;
    padding: 0; // 30px
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
    &.is-active {
      padding: 30px 0;
      max-height: 15rem;
    }
  }

  &__list {
    @include flex-v-center;
    @include m {
      display: block;
    }
  }
  &__item {
    &:not(:last-of-type) {
      margin-right: 32px;
      @include n {
        margin-right: 22px;
      }
    }
    @include m {
      margin-right: 0;
      margin-bottom: 22px;
    }
  }
  &__link {
    color: var(--color-text);
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    position: relative;
    &:hover {
      color: var(--color-text);
    }
    &.is-active {
      color: var(--color-text);
    }
  }
}
